import headers from '@/components/header_nav.vue';
import service from '../../service.js';
//插件对象
var sdkwx = uni.requireNativePlugin('RealIdentity-SdkWX');
export default{
	components: {
		headers
	},
	data(){
		return{
			title: '实名认证',
			icon: '/static/icon/left_whitch.png',
			url_left:'/pages/realName/real_name',
			name:'',
			idnum:'',
			VerifyToken:''//识别码
		}
	},
	onLoad() {
			//初始化、
			sdkwx.initialize();
	},
	methods:{
		obtain(){
			/*----------------*/
			uni.request({
				url:service.api.realName.getRealnameVerifyToken,
				method:'POST',
				data:{
				realname:this.name,
				idnum:this.idnum
				},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					let self = this;
					var res = res.data;
					console.log(res,"21321");
					if(res.code == 1){
						self.VerifyToken = res.data.VerifyToken
						self.BizId = res.data.BizId
						/* uni.showModal({
							content:self.BizId
						}) */
						if(self.VerifyToken != ''){
							sdkwx.start(self.VerifyToken, function(res){
								console.log(res , "start");
								console.log(res.code);
								console.log(self.BizId)
								// console.log(res.code);
								//var msg = JSON.stringify(res);
								if(res.state == 1){
									/* uni.showToast({
										icon:'none',
										title:'认证通过'
									}) */
									// setTimeout(()=>{
										self.submitRealname();
									// },500)
									
								}else if(res.state == -1){
									uni.showToast({
										icon:'none',
										title:'认证退出'
									})
								}else if(res.state == 2){
									uni.showToast({
										icon:'none',
										title:'认证失败'
									})
								}else{
									console.log("error");
								}
								
							});
						}
					}else{
						uni.showToast({
							title:res.info,
							icon:'none'
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title:res.info,
						icon:'none'
					});
				}
			})
			
			/*----------------*/
			
			
		},
		// 实名最后的提交
		submitRealname(){
			console.log("submitRealName")
			service.auth(this,service.api.realName.realname,"POST",{
				realname:this.name,
				idnum:this.idnum,
				BizId:this.BizId
			}, function(self,res){
				console.log(res,"123456789");
				uni.showToast({
					title:res.info,
					icon:'none'
				})
				// console.info(res.info)
				//setTimeout(function(){
					if(res.code == 1){
						uni.switchTab({
							url:'/pages/main/my'
						})
						 /* uni.reLaunch({
							url:'/pages/realName/real_name'
						}) */
					}
					
				//},1000)
				
			})
		},
	},
	onShow() {
		
	}
}
